.ge-lyrics {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
}

.ge-lyrics .ge-chord-item {
  margin: 5px 0;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
}

/* Anchor for each character */
.ge-lyrics .ge-anchor {
  position: relative;
  margin-left: 1em;
  margin-right: 3px;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 25px;
  line-height: 2em;
}

/* Chord name above anchor */
.ge-lyrics .ge-anchor .ge-anchor-chordname {
  position: absolute;
  top: -18px;
  color: indianred;
  letter-spacing: 1px;
}

/* Chord image above anchor */
.ge-lyrics .ge-anchor .ge-anchor-chordimg {
  position: absolute;
  top: -45px;
  left: -10px;
  color: indianred;
}

.ge-lyrics .ge-extra {
  display: inline-block;
  height: 25px;
  line-height: 2em;
  border-bottom: 1px solid white;
}


